/*

    font-family: 'Montserrat', sans-serif;
    font-family: 'Roboto', sans-serif;
    font-family: 'Kaushan Script', cursive;


*/
html {
  font-size: 62.5%; }

body {
  font-family: "Montserrat", sans-serif; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul, li {
  list-style: none;
  margin: 0; }

.container {
  max-width: 120rem;
  margin: 0 auto;
  padding: 0; }

.header {
  width: 100%;
  color: white;
  padding-top: 2rem;
  font-family: "Montserrat", sans-serif;
  z-index: 100; }

.header-logo {
  font-size: 3rem;
  font-weight: 800;
  letter-spacing: -0.1rem;
  color: #fff; }

.header__item-box {
  padding-top: 1.3rem; }

.item-box__nav {
  padding-top: 0.5rem;
  padding-right: 3.2rem; }

.item__link,
.icon--shopp,
.icon--magnif {
  text-transform: uppercase;
  font-size: 1.4rem;
  color: white; }
  .item__link:hover,
  .icon--shopp:hover,
  .icon--magnif:hover {
    color: #fce38a; }

.item__link:after {
  content: '';
  display: block;
  margin-top: 0.5rem;
  height: 0.3rem;
  width: 0;
  background: #fce38a; }

.item__link:hover:after {
  width: 100%; }

.list__item {
  padding-right: 2.7rem;
  padding-left: 2.7rem; }

.item-box__shopp {
  padding-right: 4.8rem; }

.icon--shopp,
.icon--magnif {
  font-size: 1.6rem; }

.item-box__magnif {
  padding-right: 0.3rem; }

.magnif__search {
  display: none; }

.section0 {
  background-image: url(../img/main_bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative; }

.section0:before {
  content: '';
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(239, 139, 138, 0.9)), to(rgba(228, 215, 135, 0.9)));
  background: -webkit-linear-gradient(top, rgba(239, 139, 138, 0.9) 0%, rgba(228, 215, 135, 0.9) 100%);
  background: -o-linear-gradient(top, rgba(239, 139, 138, 0.9) 0%, rgba(228, 215, 135, 0.9) 100%);
  background: linear-gradient(to bottom, rgba(239, 139, 138, 0.9) 0%, rgba(228, 215, 135, 0.9) 100%);
  position: absolute;
  height: 100%;
  width: 100%; }

.main-carousel {
  text-align: center;
  color: #fff; }

.main-subtitle {
  margin-top: 21.5rem;
  font-size: 7.2rem;
  font-family: 'Kaushan Script',sans-serif; }

.main-title {
  text-transform: uppercase;
  font-size: 15rem;
  font-weight: 800;
  line-height: .9;
  letter-spacing: -0.5rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  padding-left: 2.5rem; }

.divider {
  width: 6rem;
  height: 0.3rem;
  background-color: #fff;
  margin-top: 3.8rem; }

.carousel-item__btn {
  margin-top: 5.2rem;
  margin-bottom: 22.1rem; }

.btn {
  color: #fff;
  text-transform: uppercase; }
  .btn--crsl {
    padding: 0.5em 2.2em;
    font-size: 1.4rem;
    font-weight: 700;
    border: 3px solid #fff; }
    .btn--crsl:hover {
      color: #fce38a;
      border: 3px solid #fce38a; }

.carousel-indicators {
  text-transform: uppercase;
  margin: 0 auto;
  bottom: 6rem; }

.carousel-indicators li {
  position: relative;
  background-color: rgba(255, 255, 255, 0.5);
  margin: 0 0 1.5rem 0;
  width: 100%;
  max-width: 100%; }

.active li:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  width: 0;
  height: 0.3rem;
  background: rgba(243, 129, 129, 0.3);
  animation: width 5s ease; }

.indicators__link {
  width: 23.5%;
