  font-size: 1.8rem;
  margin: 0 0.5rem;
  cursor: pointer;
  text-align: left; }

.indicators__link span {
  font-weight: 700;
  font-size: 2.4rem; }

.section-box {
  padding: 9.7rem 0 10.5rem; }

.heading-box {
  text-align: center;
  color: #333;
  margin-bottom: 4.6rem; }

.main-heading {
  font-size: 2.4rem;
  font-family: 'Kaushan Script',sans-serif;
  margin-bottom: 0.2rem; }

.subheading {
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1rem; }

.paragraph {
  text-align: center;
  color: #999;
  font-size: 1.5rem;
  width: 80%;
  margin: 0 auto; }
  .paragraph-box {
    margin-bottom: 10rem; }

.divider--red {
  background-color: #f38181;
  border: none;
  margin-top: 2.8rem; }

.story__item {
  opacity: 0.9; }

.story__item-box {
  position: relative;
  width: 100%;
  transition: 0.2s linear; }
  .story__item-box:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(to bottom, rgba(239, 139, 138, 0.9) 0%, rgba(228, 215, 135, 0.9) 100%);
    opacity: 0;
    -webkit-transition: 0.2s linear;
    -o-transition: 0.2s linear;
    transition: 0.2s linear;
    cursor: pointer; }
  .story__item-box:hover {
    box-shadow: 10px 10px #95e1d3;
    transform: translate(-10px, -10px); }
    .story__item-box:hover:after {
      opacity: 1; }

.addinf__text, .icon--users {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 5;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  font-weight: bold;
  font-size: 15px;
  color: #fff;
  transition: 0.2s linear;
  opacity: 0; }

.icon--users {
  top: 43%; }

.story__item-box:hover .addinf__text,
.story__item-box:hover .icon--users {
  opacity: 1; }

.picture-item__addinf:hover .icon--users,
.picture-item__addinf:hover .addinf__text {
  color: #95e1d3; }

.item__img {
  width: 100%; }
